/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #181914; }

p {
  color: #FFFFFF;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #0da6f3;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

@media (min-width: 768px) {
  .contenedor-estetica {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    height: 100vh; } }

.contenedor-estetica .imagen {
  height: 20rem;
  background-image: url(../img/1.jpg);
  background-position: center center;
  background-size: cover; }
  @media (min-width: 768px) {
    .contenedor-estetica .imagen {
      height: 100%; } }

.app {
  width: 95%;
  margin: 0 auto;
  padding: 1.66667rem 0; }
  .app .section {
    display: none;
    margin-top: 1.66667rem; }

.mostrar-seccion {
  display: block !important; }

.tabs {
  display: grid;
  gap: 1.25rem;
  grid-template-columns: repeat(3, 1fr); }
  .tabs button {
    font-size: 1.4rem;
    padding: 1rem;
    text-align: center;
    border: none;
    text-transform: uppercase;
    color: #0da6f3;
    font-weight: 700;
    border-radius: 3px; }
    .tabs button:hover {
      cursor: pointer; }
  .tabs .actual {
    background-color: #0da6f3;
    color: #FFFFFF; }

@media (min-width: 480px) {
  .listado-servicios {
    display: grid;
    gap: 1.66667rem;
    grid-template-columns: repeat(2, 1fr); } }

.servicio {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #FFFFFF;
  border-radius: 5px;
  padding: 1.8rem 2.3rem;
  margin-bottom: 1.2rem;
  transition: all 0.5s ease-in-out; }
  .servicio p {
    color: #1a1b15;
    margin: 0;
    line-height: 1.5; }
  .servicio .nombre-servicio {
    font-size: 2.2rem; }
  .servicio .precio-servicio {
    font-size: 2.8rem;
    font-weight: 700;
    color: #0da6f3; }
  .servicio.seleccionado {
    background-color: #0da6f3; }
    .servicio.seleccionado p {
      color: #FFFFFF; }
  .servicio:hover {
    cursor: pointer; }

.paginacion {
  display: flex;
  justify-content: space-between; }
  .paginacion button {
    display: flex;
    align-items: center;
    background-color: #0da6f3;
    color: #FFFFFF;
    border: none;
    padding: 1.5rem 5rem;
    font-size: 2rem;
    font-weight: 700;
    border-radius: 5px;
    margin: 1.25rem 0;
    text-transform: uppercase; }
    .paginacion button.ocultar {
      visibility: hidden; }
    .paginacion button:hover {
      cursor: pointer; }
  .paginacion i {
    margin: 0 0.5rem; }
  .paginacion p {
    display: none;
    margin: 0; }
  @media (min-width: 1024px) {
    .paginacion p {
      display: block; } }

.formulario {
  width: 100%;
  margin: 2.5rem 0; }
  .formulario .campo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1rem 0; }
    .formulario .campo label {
      flex: 0 0 10rem;
      color: #FFFFFF; }
    .formulario .campo input {
      width: 100%;
      border-radius: 10px;
      padding: 1rem 0; }

.alerta {
  padding: 2.5rem;
  margin: 2.5rem 0;
  text-align: center;
  color: #FFFFFF;
  width: 100%;
  display: block; }
  .alerta.error {
    background-color: #c20000; }

.alerta-input {
  border: 1px solid red; }

.contenido-resumen {
  margin-bottom: 2.5rem; }
  .contenido-resumen h3 {
    color: #0da6f3;
    text-align: center;
    margin: 2.5rem 0; }
  .contenido-resumen p {
    margin-bottom: 0.5rem; }
    .contenido-resumen p span {
      font-weight: bold;
      color: #0da6f3;
      font-size: 2.4rem; }

.resumen-servicios .contenedor-servicio {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #43453c; }
  .resumen-servicios .contenedor-servicio .resumen-precio {
    font-size: 2.4rem;
    font-weight: 700;
    color: #0da6f3; }

.invalidar-cita {
  background-color: #FFFFFF;
  border-radius: 5px;
  margin: 2.5rem 0;
  padding: 1.66667rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center; }
  .invalidar-cita p {
    color: #0da6f3;
    font-weight: 700;
    font-size: 2.5rem;
    margin: 1.5rem 0; }
  .invalidar-cita i {
    font-size: 4rem;
    border-radius: 5px;
    background-color: #0da6f3;
    color: #FFFFFF;
    width: 4rem;
    height: 4rem; }

.pago {
  text-align: right;
  margin-bottom: 2.5rem;
  font-size: 3rem; }
  .pago span {
    font-size: 3rem !important;
    font-weight: 700; }
