.listado-servicios {
  @include telefono {
    @include grid(2, $separacion/3);
  }
}

.servicio {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: $blanco;
  border-radius: 5px;
  padding: 1.8rem 2.3rem;
  margin-bottom: 1.2rem;
  transition: all 0.5s ease-in-out;
  p {
    color: $negro;
    margin: 0;
    line-height: 1.5;
  }
  .nombre-servicio {
    font-size: 2.2rem;
  }
  .precio-servicio {
    font-size: 2.8rem;
    font-weight: $bold;
    color: $azul;
  }
  &.seleccionado {
    background-color: $azul;
    p {
      color: $blanco;
    }
  }

  &:hover {
    cursor: pointer;
  }
}
