.paginacion {
  display: flex;
  justify-content: space-between;

  button {
    display: flex;
    align-items: center;
    background-color: $azul;
    color: $blanco;
    border: none;
    padding: 1.5rem 5rem;
    font-size: 2rem;
    font-weight: $bold;
    border-radius: 5px;
    margin: $separacion/4 0;
    text-transform: uppercase;

    &.ocultar {
      visibility: hidden;
    }
    &:hover {
      cursor: pointer;
    }
  }

  i {
    margin: 0 0.5rem;
  }
  p {
    display: none;
    margin: 0;
  }

  @include desktop {
    p {
      display: block;
    }
  }
}
