.tabs {
  @include grid(3, $separacion/4);
  button {
    font-size: 1.4rem;
    padding: 1rem;
    text-align: center;
    border: none;
    text-transform: uppercase;
    color: $azul;
    font-weight: $bold;
    border-radius: 3px;
    &:hover {
      cursor: pointer;
    }
  }

  .actual {
    background-color: $azul;
    color: $blanco;
  }
}
