.formulario {
  width: 100%;
  margin: $separacion/2 0;

  .campo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: $separacion/5 0;

    label {
      flex: 0 0 10rem;
      color: $blanco;
    }
    input {
      width: 100%;
      border-radius: 10px;
      padding: 1rem 0;
    }
  }
}
.alerta {
  padding: $separacion/2;
  margin: $separacion/2 0;
  text-align: center;
  color: $blanco;
  width: 100%;
  display: block;

  &.error {
    background-color: rgb(194, 0, 0);
  }
}

.alerta-input {
  border: 1px solid red;
}
