.contenido-resumen {
  margin-bottom: $separacion/2;

  h3 {
    color: $azul;
    text-align: center;
    margin: $separacion/2 0;
  }

  p {
    margin-bottom: 0.5rem;
    span {
      font-weight: bold;
      color: $azul;
      font-size: 2.4rem;
    }
  }
}

.resumen-servicios {
  .contenedor-servicio {
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid #43453c;
    .resumen-precio {
      font-size: 2.4rem;
      font-weight: $bold;
      color: $azul;
    }
  }
}

.invalidar-cita {
  background-color: $blanco;
  border-radius: 5px;
  margin: $separacion/2 0;
  padding: $separacion/3;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  p {
    color: $azul;
    font-weight: $bold;
    font-size: 2.5rem;
    margin: 1.5rem 0;
  }

  i {
    font-size: 4rem;
    border-radius: 5px;
    background-color: $azul;
    color: $blanco;
    width: 4rem;
    height: 4rem;
  }
}

.pago {
  text-align: right;
  margin-bottom: $separacion/2;
  font-size: 3rem;
  span {
    font-size: 3rem !important;
    font-weight: $bold;
  }
}
