.contenedor-estetica {
    @include tablet {
        @include grid(2, 0);
        height: 100vh;
    }
    .imagen {
        height: 20rem;
        @include tablet {
            height: 100%;
        }
        background-image: url(../img/1.jpg);
        background-position: center center;
        background-size: cover;
    }
}

.app {
    width: 95%;
    margin: 0 auto;
    padding: $separacion/3 0;
    .section {
        display: none;
        margin-top: $separacion/3;
    }
}

.mostrar-seccion {
    display: block!important;
}